@keyframes pulse {
  0% {
    transform: scale(0.95);
  }

  70% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.95);
  }
}

.wp-block-widget-werstreamtes {

  .wse-widget-container {

    display: flex;
    justify-content: center;
    align-items: center;

    .wse-loading {

      text-align: center;
      font-size: 11px;
      animation: pulse 1.5s infinite;

      img {
        margin: 0 auto;
        height: 50px;
      }

    }

    ol {
      margin-bottom: inherit !important;
    }

  }

}